import * as React from "react";
import Grid from "@mui/material/Grid";
import Typography from "@mui/material/Typography";
import TextField from "@mui/material/TextField";
import FormControlLabel from "@mui/material/FormControlLabel";
import Checkbox from "@mui/material/Checkbox";

export default function ClaimInfo() {
  return (
    <React.Fragment>
        <Typography variant="h6" gutterBottom>Datos personales</Typography>

        <Grid container spacing={3}>
            <Grid item xs={12} sm={6}>
                <TextField
                    required
                    id="firstName"
                    name="firstName"
                    label="Nombre"
                    fullWidth
                    autoComplete="given-name"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12} sm={6}>
                <TextField
                    required
                    id="lastName"
                    name="lastName"
                    label="Apellidos"
                    fullWidth
                    autoComplete="family-name"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12} sm={6}>
                <TextField
                    required
                    id="phoneNum"
                    name="phoneNum"
                    label="Numero de teléfono"
                    fullWidth
                    autoComplete="family-name"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12} sm={6}>
                <TextField
                    required
                    id="email"
                    name="email"
                    label="Direccion de correo electrónico"
                    fullWidth
                    autoComplete="family-name"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12}>
                <TextField
                    required
                    id="address1"
                    name="address1"
                    label="Localidad"
                    fullWidth
                    autoComplete="shipping address-line1"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12}>
                <TextField
                    id="address2"
                    name="address2"
                    label="Calle"
                    fullWidth
                    autoComplete="shipping address-line2"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12} sm={6}>
                <TextField
                    required
                    id="doorNum"
                    name="doorNum"
                    label="Numero/Escalera/Puerta"
                    fullWidth
                    autoComplete="shipping address-level2"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12} sm={6}>
                <TextField
                    id="province"
                    name="province"
                    label="Provincia"
                    fullWidth
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12} sm={6}>
                <TextField
                    required
                    id="zip"
                    name="zip"
                    label="Codigo Postal"
                    fullWidth
                    autoComplete="shipping postal-code"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12} sm={6}>
                <TextField
                    required
                    id="country"
                    name="country"
                    label="Pais"
                    fullWidth
                    autoComplete="shipping country"
                    variant="standard"
                />
            </Grid>

            <Grid item xs={12}>
                <FormControlLabel
                    control={ <Checkbox color="secondary" name="acceptRgpd" value="yes" /> }
                    label="Acepto que se lleve a cabo el tratamiento de mis datos tal y como se detalla en el Reglamento General de Protección de Datos"
                />
            </Grid>
        </Grid>
    </React.Fragment>
  );
}
